body
{
  background: rgb(44, 45, 48);
  min-height: 100vh;
  margin: 0px;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
}
body, html, * {
  box-sizing: border-box;
  font-family: Inter, Roboto, Arial;
  color: white;
  user-select: none;
  text-rendering: optimizespeed;
  -webkit-font-smoothing: antialiased;
}
.iframeParentDiv {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.mainFrame {
  width: 100%;
  height: 100%;
}
.iframeDiv {
  position: relative;
}
.clickDiv {
  position: absolute;
  top: 22px;
  right: 32px;
  width: 157px;
  height: 70px;
  cursor: pointer;
}
.hideDiv {
  display: none;
}

@media (max-width: 768px) {
  .clickDiv {
    position: absolute;
    top: 112px;
    left: 38px;
    width: 157px;
    height: 40px;
    cursor: pointer;
  }
}