.wallet-adapter-modal.wallet-adapter-modal-fade-in {
    opacity: 1;
}
.wallet-adapter-modal {
    background: rgba(0,0,0,.5);
    bottom: 0;
    left: 0;
    opacity: 0;
    overflow-y: auto;
    position: fixed;
    right: 0;
    top: 0;
    transition: opacity .15s linear;
    z-index: 1040;
}
.wallet-adapter-modal-container {
    align-items: center;
    display: flex;
    justify-content: center;
    margin: 3rem;
    min-height: calc(100vh - 6rem);
}

.wallet-adapter-modal-wrapper {
    align-items: center;
    background: #10141f;
    border-radius: 10px;
    box-shadow: 0 8px 20px rgb(0 0 0 / 60%);
    box-sizing: border-box;
    display: flex;
    flex: 1;
    flex-direction: column;
    font-family: DM Sans,Roboto,Helvetica Neue,Helvetica,Arial,sans-serif;
    max-width: 400px;
    position: relative;
    z-index: 1050;
}

.wallet-adapter-modal-button-close {
    align-items: center;
    background: #1a1f2e;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    padding: 12px;
    position: absolute;
    right: 18px;
    top: 18px;
}

.wallet-adapter-modal-title {
    color: #fff;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
    margin: 0;
    padding: 64px 48px 48px;
    text-align: center;
}

.wallet-adapter-modal-list {
    list-style: none;
    margin: 0 0 50px;
    padding: 0;
    width: 100%;
}

li {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    margin: 0.5rem 0;
}

.wallet-adapter-modal-list .wallet-adapter-button {
    border-radius: 0;
    font-size: 18px;
    font-weight: 400;
}

.wallet-adapter-modal-wrapper .wallet-adapter-button {
    width: 100%;
}

.wallet-adapter-button {
    align-items: center;
    background-color: transparent;
    border: none;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    display: flex;
    font-family: DM Sans,Roboto,Helvetica Neue,Helvetica,Arial,sans-serif;
    font-size: 16px;
    font-weight: 600;
    height: 48px;
    line-height: 48px;
    padding: 0 24px;
}

.wallet-adapter-button-start-icon {
